import { merge } from 'lodash-es';

import { DeepPartial } from '@yandex-music-int/ts-utility-types';

import { getPlatformSecret } from '@/shared/lib/platform/getPlatformSecret.js';

import { TLD_MARK } from '../router/constants/tldMark.js';

import { config as defaultConfig } from './default.js';
import { getPrefixUrl } from './getPrefixUrl.js';
import { musicExternalApiRetryPolicyConfig } from './retryPolicyConfigs/musicExternalApiRetryPolicyConfig.js';
import { ConfigVariables } from './types/ConfigVariables.js';

export const config = (): ConfigVariables => {
    return merge(defaultConfig(), {
        resources: {
            musicExternalApi: {
                prefixUrl: getPrefixUrl(`https://api.music.qa.yandex.${TLD_MARK}`),
                retryPolicyConfig: musicExternalApiRetryPolicyConfig
            }
        },
        player: {
            overembed: false,
            secretKey: getPlatformSecret(),
            externalDomain: 'next.qa.music.yandex.ru',
            prefixUrl: getPrefixUrl(`https://api.music.qa.yandex.${TLD_MARK}`)
        },
        dev: {
            // TODO: Показываем dev панель на qa окружении, при production конфигурации надо поставить false
            panel: true
        },
        metrics: {
            serverAddress: '/metrics'
        },
        oldWebHost: `music.qa.yandex.${TLD_MARK}`
    } satisfies DeepPartial<ConfigVariables>);
};
